:root {
  --blue-color: #05386b;
  --lightBlue-color: #379683;
  --green-color: #5cdb95;
  --lightGreen-color: #8ee4af;
  --sand-color: #edf5e1;
}

* {
  box-sizing: border-box;

  font-family: 'Times New Roman', Times, serif;

  margin: 0;
  padding: 0;
}

body {
  background: url(https://media.gettyimages.com/vectors/watercolor-mountains-green-vector-id1174820578);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  height: 100vh;
}
